import React, { useEffect, useState } from "react";
import logo from "./css/logo.png";
import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap/dist/js/bootstrap.bundle.min.js";

function Navbar() {
  const [userId, setUserId] = useState(null);

  useEffect(() => {
    //    
    const fetchSessionInfo = async () => {
      try {
        const response = await fetch("/dlt/member/session", {
          credentials: "include", //  ,   .
        });
        if (!response.ok) {
          throw new Error("Network response was not ok");
        }

        const data = await response.json();
        setUserId(data.user_id);
      } catch (error) {
        console.error("Error fetching session info:", error);
      }
    };

    fetchSessionInfo();
  }, []);

  return (
    <nav
      id="topnav"
      className="navbar navbar-expand-lg bg-white navbar-light px-4 px-lg-5"
    >
      <a
        href="/dlt"
        className="navbar-brand d-flex align-items-center"
        id="mainLogoBox"
      >
        <img src={logo} alt="Logo" />
      </a>
      <button
        type="button"
        className="navbar-toggler me-0"
        data-bs-toggle="collapse"
        data-bs-target="#navbarCollapse"
      >
        <span className="navbar-toggler-icon"></span>
      </button>
      <div className="collapse navbar-collapse" id="navbarCollapse">
        <div className="navbar-nav ms-auto p-4 p-lg-0">
          <a href="/dlt/resources/build/index.html" className="nav-item nav-link"> </a>
          <a href="/dlt/life" className="nav-item nav-link"> </a>
          <a href="/dlt/community/list?page=1" className="nav-item nav-link"></a>
          {userId === null ? (
            <a href="/dlt/member/login" className="nav-item nav-link">
              
            </a>
          ) : (
            <a href="/dlt/member/logout" className="nav-item nav-link">
              
            </a>
          )}
        </div>
      </div>
    </nav>
  );
}

export default Navbar;
